.persons{
  margin-top: 55px;

  &__list{
    max-width: 295px;
    column-gap: 37px;
  }

  &__content{
    margin-top: 65px;
  }

  &__list{
    display: flex;
  }

  &__tab{
    display: none;
  }

  &__tab--active{
    display: block;
  }

  &__list-people{
    display: grid;
    grid-template-columns: repeat(12,1fr);
    gap: 28px;
  }

  &__item-people{
    grid-column: 4 span;
  }

  &__list-table{
    border-radius: 10px;
    background: var(--mirage);
    max-height: 444px;
  }

  &__item-rating{
    border-bottom: 2px solid var(--mirage-2);
    padding: 20px 40px 20px 33px;
  }

  &__text{
    margin-bottom: 7px;
  }
}


@media (max-width: 1400px){

  .persons{

    &__menu{
      flex-direction: column;
      row-gap: 10px;
    }

    &__content{
      margin-top: 27px;
    }

    &__list-people{
      display: grid;
      grid-template-columns: repeat(8,1fr);
      gap: 20px;
    }

    &__item-people{
      grid-column: 4 span;
    }

    &__item-people:last-child{
      grid-column: 8 span;
    }
  }
}

 @media (max-width: 800px){

   .persons{

    &__list-people{
      gap: 20px;
    }

    &__item-rating{
      padding: 8px 38px 8px 19px;
    }

    &__text{
      margin: 0;
    }
  }
}


 @media (max-width: 525px){

  .persons{

    &__list-people{
      gap: 10px;
    }

    &__list{
      display: none;
    }

    &__t2{
      position: relative;

      &::after{
        content: url(../img/svg/bur.svg);
        top: -2px;
        right: -24px;
        position: absolute;
      }
    }
  }
}
